














































import { Vue, Component } from 'vue-property-decorator';
import { mapState, mapMutations } from 'vuex';
import eventBus from '@/eventBus';
import { Place } from '@/models/place';
import Fuse from 'fuse.js';

@Component({
  computed: {
    ...mapState(['drawer', 'searchClosed']),
  },
  methods: {
    ...mapMutations(['toggleDrawer']),
  },
})
export default class AppBar extends Vue {
  width = '80';
  searchText = '';
  fuse = new Fuse<Place, Fuse.FuseOptions<Place>>(this.$store.state.placeList, {
    shouldSort: true,
    includeMatches: true,
    threshold: 0.6,
    location: 0,
    distance: 100,
    maxPatternLength: 32,
    minMatchCharLength: 1,
    keys: ['properties.name', 'properties.type', 'properties.id'],
  });

  collapseBar() {
    this.$store.commit('closeSearch');
    this.searchText = '';
  }

  expandBar() {
    this.$store.commit('openSearch');
  }

  onBlur() {
    if (!this.searchIsBusy) {
      this.collapseBar();
    }
  }

  goTo(place: Place) {
    this.searchText = '';
    this.collapseBar();
    this.$router.push(`/place/${place.properties.id}`);
  }

  get searchIsBusy() {
    return this.searchText.length > 0 && this.filteredPlaces.length > 0;
  }

  get filteredPlaces() {
    return this.fuse.search<Array<Place>>(this.searchText);
  }

  get right() {
    let width = window.innerWidth > 0 ? window.innerWidth : screen.width;
    // eslint-disable-next-line no-console
    console.log(width);
    let isMobile = width >= 320 && width <= 767;
    return isMobile ? '0' : '50%';
  }

  mounted() {
    eventBus.$on('openSearch', () => {
      this.expandBar();
    });

    eventBus.$on('closeSearch', () => {
      this.collapseBar();
    });
  }
}
